import React from "react";

import "./Workflow.css";

import blacklists from "../../assets/icon-blacklist.svg";
import text from "../../assets/icon-text.svg";
import preview from "../../assets/icon-preview.svg";

function Workflow() {
  const workflowDescription = {
    descriptions: [
      {
        img: blacklists,
        alt: "ai icon",
        header3: "Create blacklists",
        pg:
          "Ensure sensitive information never makes its way to your clipboard by excluding certain sources.",
      },
      {
        img: text,
        alt: "letter icon",
        header3: "Plain text snippets",
        pg:
          "Remove unwanted formatting from copied text for a consistent look.",
      },
      {
        img: preview,
        alt: "eye icon",
        header3: "Sneak preview",
        pg: "Quick preview of all snippets on your Clipboard for easy access.",
      },
    ],
  };
  return (
    <div className="Workflow">
      <div>
        <h2>Supercharge your workflow</h2>
        <p>We’ve got the tools to boost your productivity.</p>
        <div className="WorkflowDescriptionLayout">
          {workflowDescription.descriptions.map((des) => {
            return (
              <div className="WorkflowDescription">
                <img src={des.img} alt={des.alt} />
                <h3>{des.header3}</h3>
                <p>{des.pg}</p>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
export default Workflow;
