import React from "react";

import "./Testimonials.css";

import google from "../../assets/logo-google.png";
import ibm from "../../assets/logo-ibm.png";
import microsoft from "../../assets/logo-microsoft.png";
import hp from "../../assets/logo-hp.png";
import vector from "../../assets/logo-vector-graphics.png";

function Testimonials() {
  const allImages = {
    images: [
      { src: google, alt: "google logo" },
      { src: ibm, alt: "ibm logo" },
      { src: microsoft, alt: "microsoft logo" },
      { src: hp, alt: "hp logo" },
      { src: vector, alt: "vector logo" },
    ],
  };
  return (
    <div className="TestimonialsLayout">
      {allImages.images.map((img) => {
        return (
          <div className="Testimonials">
            <img src={img.src} alt={img.alt} />
          </div>
        );
      })}
    </div>
  );
}

export default Testimonials;
