import React from "react";
import Computer from "../../assets/image-computer.png";
import "./Desktop.css";

function Desktop() {
  const snippetsTopics = {
    topics: [
      {
        header: "Quick Search",
        pg:
          " Easily search your snippets by content, category, web address, application, and more.",
      },
      {
        header: "iCloud Sync",
        pg: " Instantly saves and syncs snippets across all your devices.",
      },
      {
        header: "Complete History",
        pg:
          "  Retrieve any snippets from the first moment you started using the app.",
      },
    ],
  };
  return (
    <div className="Desktop">
      <div>
        <img className="Img" src={Computer} alt="computer" />
      </div>
      <div className="Snippets">
        {snippetsTopics.topics.map((topics) => {
          return (
            <div>
              <h3>{topics.header}</h3>
              <p>{topics.pg}</p>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default Desktop;
