import React from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";

function Buttons() {
  const useStyles = makeStyles({
    button1: {
      backgroundColor: "hsl(171, 66%, 44%)",
      borderRadius: "50px",
      boxShadow: "0px 0px 5px 0px #000000",
      color: "white",
      margin: "30px 10px 5px",
      padding: "15px",
      width: "250px",
      "&:hover": {
        backgroundColor: "hsl(171, 72%, 50%)",
      },
    },
    button2: {
      backgroundColor: "hsl(233, 100%, 69%)",
      borderRadius: "50px",
      boxShadow: "0px 0px 5px 0px #000000",
      color: "white",
      margin: "30px 10px 5px",
      padding: "15px",
      width: "250px",
      "&:hover": {
        backgroundColor: "hsl(233, 86%, 72%)",
      },
    },
  });

  const classes = useStyles();

  return (
    <div>
      <Button className={classes.button1}>Download for IOS</Button>
      <Button className={classes.button2}>Download for Mac</Button>
    </div>
  );
}

export default Buttons;
